import gsap from 'gsap'

const dropdownTrigger = document.querySelectorAll('.dropdown__trigger')

dropdownTrigger.forEach((btn) => {
	btn.addEventListener('click', () => {
		const dropdown = btn.closest('.dropdown').querySelector('.dropdown__body')

		btn.classList.toggle('active')

		if (btn.classList.contains('active')) {
			gsap.to(dropdown, {
				duration: 0.7,
				ease: 'power4.easeIn',
				height: 'auto',
			})
		} else {
			gsap.to(dropdown, {
				duration: 0.7,
				ease: 'power4.out',
				height: 0,
			})
		}
	})
})
