import gsap from 'gsap'
import ScrollTrigger from 'gsap/ScrollTrigger'
import { desktop } from './check-data'

gsap.registerPlugin(ScrollTrigger)

const grid = document.querySelector('[data-grid-second]')

if (desktop && grid) {
	const gridImages = grid.querySelectorAll('.team__item')
	const middleIndex = Math.floor(gridImages.length / 2)

	gsap
		.timeline({
			defaults: {
				ease: 'power3',
			},
			scrollTrigger: {
				trigger: grid,
				start: 'center center',
				end: '+=250%',
				pin: true,
				anticipatePin: 1,
				scrub: 0.5,
			},
		})
		.from(gridImages, {
			stagger: {
				amount: 0.3,
				from: 'center',
			},
			y: window.innerHeight,
			transformOrigin: '50% 0%',
			rotation: (pos) => {
				const distanceFromCenter = Math.abs(pos - middleIndex)
				return pos < middleIndex ? distanceFromCenter * 3 : distanceFromCenter * -3
			},
		})
		// text content
		.from(
			grid.querySelectorAll('.team__item-inner'),
			{
				stagger: {
					amount: 0.3,
					from: 'center',
				},
				yPercent: 100,
				autoAlpha: 0,
			},
			0
		)
}
