import gsap from 'gsap'
import { homePage } from './check-data.js'
import { smoother } from './smooth-scroll.js'
import ScrollTrigger from 'gsap/ScrollTrigger'
import ScrollToPlugin from 'gsap/ScrollToPlugin'

gsap.registerPlugin(ScrollTrigger, ScrollToPlugin)

const transitionPlug = document.querySelector('.transition-plug')
const transitionPlugInner = document.querySelector('.transition-plug__inner')

// Transition links
const transitionLinksTl = gsap
	.timeline({
		paused: true,
		defaults: {
			ease: 'power4.inOut',
			duration: 2,
		},
	})
	.to(
		transitionPlugInner,
		{
			top: 0,
			bottom: 'auto',
			duration: 0,
		},
		0
	)
	.to(
		transitionPlug,
		{
			translateY: '-105%',
			duration: 0,
		},
		'<'
	)
	.to(
		transitionPlug,
		{
			translateY: 0,
		},
		'>'
	)
	.to(
		transitionPlugInner,
		{
			height: '100%',
		},
		'<+=0.05'
	)

const linkTransition = (target) => {
	target.addEventListener('click', (e) => {
		if (!target.hasAttribute('target')) {
			e.preventDefault()

			let url = target.getAttribute('href')

			transitionLinksTl.play()

			if (target.dataset.section) {
				localStorage.setItem('scrolltoSection', target.dataset.section)
			}

			setTimeout(function () {
				window.location = url
			}, 2500)

			window.addEventListener('unload', function () {
				transitionLinksTl.reverse()
			})
		}
	})
}
const tranistionLinks = document.querySelectorAll('a')

tranistionLinks.forEach((link) => {
	linkTransition(link)
})
//

if (!homePage) {
	let section = localStorage.getItem('scrolltoSection')

	const currentPageTl = gsap
		.timeline({
			paused: true,
			defaults: {
				ease: 'power4.inOut',
				duration: 2,
			},
		})
		.fromTo(
			transitionPlugInner,
			{
				top: 'auto',
				bottom: 0,
				duration: 0,
			},
			{
				height: '0%',
			},
			0
		)
		.to(
			transitionPlug,
			{
				translateY: '105%',
			},
			'<+=0.05'
		)

	if (document.querySelector('[data-animated-main-text]')) {
		const animatedMainTextChars = document.querySelectorAll(
			'[data-animated-main-text] .char'
		)

		currentPageTl.to(
			animatedMainTextChars,
			{
				duration: 1.5,
				ease: 'back.inOut(2)',
				opacity: 1,
				yPercent: 0,
				scaleY: 1,
				scaleX: 1,
				stagger: 0.03,
			},
			'<+=0.5'
		)
	}

	// if (document.querySelector('.main-opacity-block')) {
	// 	currentPageTl.to(
	// 		'.main-opacity-block',
	// 		{
	// 			opacity: 1,
	// 			duration: 1,
	// 		},
	// 		'<+=1'
	// 	)
	// }

	gsap.set(transitionPlug, {
		translateY: 0,
	})

	gsap.set(transitionPlugInner, {
		height: '100%',
	})

	window.addEventListener('load', () => {
		if (section) {
			smoother.scrollTo(section)
			localStorage.removeItem('scrolltoSection')
		}

		currentPageTl.play()
	})
}
