import gsap from 'gsap'
import ScrollSmoother from 'gsap/ScrollSmoother'
import ScrollTrigger from 'gsap/ScrollTrigger'
import { desktop, mobile } from './check-data'

gsap.registerPlugin(ScrollTrigger, ScrollSmoother)

let smoother

if (desktop) {
	smoother = ScrollSmoother.create({
		smooth: 2.5,
		speed: 0.7,
		normalizeScroll: true,
	})
}

if (mobile) {
	smoother = ScrollSmoother.create({
		smooth: 1,
		speed: 1,
		normalizeScroll: true,
		ignoreMobileResize: true,
	})
}

// Scroll to sections
const scrollToBtns = document.querySelectorAll('[data-scroll-to]')

if (scrollToBtns.length) {
	scrollToBtns.forEach((btn) => {
		btn.addEventListener('click', (e) => {
			e.preventDefault()

			btn.disabled = true

			let section = btn.dataset.scrollTo
			let offset = smoother.offset(`#${section}`, 'top top')

			gsap
				.timeline({
					defaults: {
						ease: 'power4.inOut',
						duration: 2,
					},
				})
				.to(
					'.transition-plug__inner',
					{
						top: 0,
						bottom: 'auto',
						duration: 0.1,
					},
					0
				)
				.to(
					'.transition-plug',
					{
						translateY: '-105%',
						duration: 0,
					},
					'<'
				)
				.to(
					'.transition-plug',
					{
						translateY: 0,
					},
					'>'
				)
				.to(
					'.transition-plug__inner',
					{
						height: '100%',
					},
					'<+=0.05'
				)
				.to(
					smoother,
					{
						scrollTop: offset,
						duration: 0.1,
						ease: 'none',
					},
					'>'
				)
				.fromTo(
					'.transition-plug__inner',
					{
						top: 'auto',
						bottom: 0,
						duration: 0,
					},
					{
						height: '0%',
					},
					'>+=2'
				)
				.to(
					'.transition-plug',
					{
						translateY: '105%',
						onComplete: () => {
							btn.disabled = false
						},
					},
					'<+=0.05'
				)
		})
	})
}
//

export { smoother }
