import { desktop } from './check-data.js'

import gsap from 'gsap'
import SplitText from 'gsap/SplitText'

gsap.registerPlugin(SplitText)

document.addEventListener('DOMContentLoaded', () => {
	const textHover = document.querySelectorAll('.text-hover')

	if (desktop && textHover.length) {
		textHover.forEach((text) => {
			const textElems = text.querySelectorAll('.text-hover__elem')
			if (!textElems.length) return

			textElems.forEach(
				(el) => new SplitText(el, { type: 'chars, words', charsClass: 'char' })
			)

			let textHover1 = text.querySelectorAll('.text-hover__elem-1 .char')
			let textHover2 = text.querySelectorAll('.text-hover__elem-2 .char')

			if (!textHover1.length && !textHover2.length) return

			const tlTextHover = gsap.timeline({
				paused: true,
				defaults: { stagger: 0.015, duration: 0.35, ease: 'power3.easeOut' },
			})

			if (textHover1.length) tlTextHover.to(textHover1, { yPercent: -120 })
			if (textHover2.length) tlTextHover.to(textHover2, { yPercent: -100 }, 0)

			text.addEventListener('mouseenter', () => tlTextHover.play())
			text.addEventListener('mouseleave', () => tlTextHover.reverse())
		})
	}
})
