import { desktop, mobile } from './check-data.js'

import 'swiper/css'
import Swiper from 'swiper'
import { Pagination, Autoplay } from 'swiper/modules'

const teamSlider = document.querySelector('.team__container')
const clientsSlider = document.querySelector('.clients__container')

// Team (mobile)
if (mobile && teamSlider) {
	new Swiper(teamSlider, {
		modules: [Pagination],
		slidesPerView: 'auto',
		spaceBetween: '10rem',
		freeMode: true,
		speed: 1000,
		wrapperClass: 'team__container-inner',
		slideClass: 'team__item',
		pagination: {
			el: '.team__progressbar',
			progressbarFillClass: 'team__progressbar-fill',
			type: 'progressbar',
		},
	})
}
//

// Clients (desktop)
if (desktop && clientsSlider) {
	new Swiper(clientsSlider, {
		modules: [Autoplay],
		slidesPerView: 'auto',
		spaceBetween: '20rem',
		loop: true,
		freeMode: true,
		freeModeMomentum: false,
		allowTouchMove: false,
		speed: 6000,
		wrapperClass: 'clients__container-inner',
		slideClass: 'clients__item',
		autoplay: {
			delay: 0,
			disableOnInteraction: false,
		},
	})
}
//
