import gsap from 'gsap'
import ScrollTrigger from 'gsap/ScrollTrigger'
import { desktop } from './check-data'

gsap.registerPlugin(ScrollTrigger)

const contentElements = document.querySelectorAll('.reviews__item')
const totalContentElements = contentElements.length

if (desktop && contentElements.length) {
	window.addEventListener('load', () => {
		const reviewsTl = gsap.timeline({
			scrollTrigger: {
				trigger: '.reviews__container',
				start: 'center center',
				end: () => `+=${totalContentElements * window.innerHeight}`,
				pin: true,
				anticipatePin: 1,
				scrub: true,
			},
		})

		contentElements.forEach((el, index) => {
			if (index == 0) {
				return
			} else if (index == 1) {
				reviewsTl.to(
					el,
					{
						ease: 'none',
						yPercent: -100,
					},
					0
				)
			} else {
				reviewsTl.to(
					el,
					{
						ease: 'none',
						yPercent: -100,
					},
					'>'
				)
			}
		})
	})
}
