import { desktop } from './check-data.js'

import gsap from 'gsap'
import ScrollTrigger from 'gsap/ScrollTrigger'

gsap.registerPlugin(ScrollTrigger)

// Show on scroll
const opacityBlocks = document.querySelectorAll('[data-opacity-block]')

if (desktop && opacityBlocks.length) {
	opacityBlocks.forEach((block) => {
		gsap.set(block, {
			opacity: 0,
		})

		gsap.to(block, {
			ease: 'power4.inOut',
			opacity: 1,
			duration: 1,
			scrollTrigger: {
				trigger: block,
				start: 'center 92%',
				markers: true,
			},
		})
	})
}
//

// Hero section
const mainOpacityBlocks = document.querySelectorAll('[data-opacity-main-block]')

if (mainOpacityBlocks.length) {
	mainOpacityBlocks.forEach((block) => {
		gsap.set(block, {
			opacity: 0,
		})
	})
}
//
