import { desktop, homePage } from './check-data.js'

import gsap from 'gsap'
import SplitText from 'gsap/SplitText'
import ScrollTrigger from 'gsap/ScrollTrigger'

gsap.registerPlugin(ScrollTrigger, SplitText)

// Text animation main screen
const animatedMainText = document.querySelectorAll('[data-animated-main-text]')

if (animatedMainText.length) {
	let textSplit = new SplitText(animatedMainText, {
		type: 'chars, words',
		charsClass: 'char',
		wordsClass: 'word',
	})

	gsap.set(textSplit.chars, {
		'will-change': 'opacity, transform',
		opacity: 0,
		yPercent: 120,
		scaleY: 2.3,
		scaleX: 0.7,
		transformOrigin: '50% 0%',
	})

	if (homePage) {
		gsap.to(textSplit.chars, {
			duration: 1.5,
			ease: 'back.inOut(2)',
			opacity: 1,
			yPercent: 0,
			scaleY: 1,
			scaleX: 1,
			stagger: 0.03,
		})
	}
}
//

// Text animation on scroll
const animatedText = document.querySelectorAll('[data-animated-text]')

if (desktop && animatedText.length) {
	animatedText.forEach((text) => {
		let textSplit = new SplitText(text, {
			type: 'chars, words',
			charsClass: 'char',
			wordsClass: 'word',
		})

		gsap.set(textSplit.chars, {
			'will-change': 'opacity, transform',
			opacity: 0,
			yPercent: 120,
			scaleY: 2.3,
			scaleX: 0.7,
			transformOrigin: '50% 0%',
		})

		gsap.to(textSplit.chars, {
			duration: 1.5,
			ease: 'back.inOut(2)',
			opacity: 1,
			yPercent: 0,
			scaleY: 1,
			scaleX: 1,
			stagger: 0.03,
			scrollTrigger: {
				trigger: text,
				start: 'center bottom+=50%',
				end: 'center 40%',
				once: true,
				scrub: true,
			},
		})
	})
}
//

// Reading
const readingBlock = document.querySelectorAll('[data-reading-block]')

if (readingBlock.length) {
	readingBlock.forEach((block) => {
		const readingText = block.querySelectorAll('[data-reading-text]')

		const textSplit = new SplitText(readingText, {
			type: 'chars, words',
			charsClass: 'char',
			wordsClass: 'word',
		})

		gsap.set(textSplit.words, {
			'will-change': 'opacity',
			opacity: 0.1,
		})

		gsap.to(textSplit.words, {
			ease: 'none',
			opacity: 1,
			stagger: 0.05,
			scrollTrigger: {
				trigger: block,
				start: 'top center',
				end: 'bottom center',
				scrub: true,
			},
		})
	})
}
//
