import gsap from 'gsap'
import ScrollTrigger from 'gsap/ScrollTrigger'

gsap.registerPlugin(ScrollTrigger)

ScrollTrigger.create({
	trigger: '.header',
	start: 'top top',
	end: '+=10000%',
	pin: true,
	pinSpacing: false,
	anticipatePin: 2,
	scrub: true,
})

const header = document.querySelector('.header')
const whiteHeaderArea = document.querySelector('.white-header-area')

if (whiteHeaderArea) {
	ScrollTrigger.create({
		trigger: whiteHeaderArea,
		start: 'top 10%',
		end: 'bottom top',
		onEnter() {
			header.classList.add('_white-version')
		},
		onLeave() {
			header.classList.remove('_white-version')
		},
		onEnterBack() {
			header.classList.add('_white-version')
		},
		onLeaveBack() {
			header.classList.remove('_white-version')
		},
	})
}
