import gsap from 'gsap'
import { smoother } from './smooth-scroll.js'
import { mobile } from './check-data.js'

if (mobile) {
	const mobileFixedBtn = document.querySelector('.mobile-fixed-btn')
	const headerContainer = document.querySelector('.header__container')
	const hamburgerMenu = document.querySelector('.header__hamburger')
	const hamburgerBtn = document.querySelector('.header__hamburger-btn')
	const hamburgerTrigger = document.querySelectorAll(
		'.header__hamburger-btn, .header__menu-item, .header__btn'
	)

	hamburgerTrigger.forEach((btn) => {
		btn.addEventListener('click', () => {
			headerContainer.classList.toggle('active')
			hamburgerBtn.classList.toggle('active')
			hamburgerMenu.classList.toggle('active')
			mobileFixedBtn.classList.toggle('active')

			let hamburgerActive = btn.classList.contains('active')

			if (hamburgerActive) {
				smoother.paused(true)

				gsap.to(hamburgerMenu, {
					duration: 1.5,
					translateX: 0,
					ease: 'power4.inOut',
				})
			} else {
				smoother.paused(false)

				gsap.to(hamburgerMenu, {
					duration: 1.5,
					translateX: '100%',
					ease: 'power4.inOut',
				})
			}
		})
	})
}
