import { Fancybox } from '@fancyapps/ui'
import '@fancyapps/ui/dist/fancybox/fancybox.css'
import { smoother } from './smooth-scroll'

const triggersContactModal = document.querySelectorAll('[data-contact-modal]')
const triggersQuestionModal = document.querySelectorAll('[data-question-modal]')

triggersContactModal.forEach((btn) => {
	btn.addEventListener('click', () => {
		Fancybox.show([
			{
				dragToClose: false,
				dragToPan: false,
				src: '#modal-contact',
				type: 'inline',
			},
		])
	})
})

triggersQuestionModal.forEach((btn) => {
	btn.addEventListener('click', () => {
		Fancybox.show([
			{
				dragToClose: false,
				dragToPan: false,
				src: '#modal-question',
				type: 'inline',
			},
		])
	})
})

Fancybox.bind('[data-fancybox]', {
	dragToClose: false,
	dragToPan: false,
	on: {
		init: () => {
			smoother.paused(true)
		},
		destroy: () => {
			smoother.paused(false)
		},
	},
})
