import gsap from 'gsap'
import ScrollTrigger from 'gsap/ScrollTrigger'
import SplitText from 'gsap/SplitText'
import { desktop, homePage } from './check-data'

gsap.registerPlugin(ScrollTrigger, SplitText)

if (desktop && homePage) {
	// Slideshow class
	class Slider {
		#current = 0
		constructor(element, reverseDirection = false) {
			this.element = element

			// Reverse direction
			this.reverseDirection = reverseDirection
			this.items = [...this.element.querySelectorAll('.slider__item')]
			this.itemsInner = this.items.map((item) =>
				item.querySelector('.slider__item-inner')
			)

			// Set current
			this.items[this.current].classList.add('slider__item--current')

			// Total items
			this.itemsTotal = this.items.length
			gsap.set([this.items, this.itemsInner], {
				'will-change': 'transform',
			})
		}
		next() {
			this.navigate(1)
		}
		prev() {
			this.navigate(-1)
		}
		get current() {
			return this.#current
		}
		set current(value) {
			this.#current = value
		}

		// direction: 1 || -1 (next || prev)
		navigate(direction) {
			const previous = this.current
			const nextIndex = previous + direction

			if (nextIndex < 0 || nextIndex > this.itemsTotal - 1) {
				return false
			}

			this.current =
				direction === 1
					? this.current < this.itemsTotal - 1
						? ++this.current
						: 0
					: this.current > 0
					? --this.current
					: this.itemsTotal - 1

			const currentItem = this.items[previous]
			const currentInner = this.itemsInner[previous]
			const upcomingItem = this.items[this.current]
			const upcomingInner = this.itemsInner[this.current]

			gsap
				.timeline({
					defaults: { duration: 1.1, ease: 'power3.inOut' },
					onComplete: () => {
						this.items[previous].classList.remove('slider__item--current')
						this.items[this.current].classList.add('slider__item--current')
					},
				})
				.to(currentItem, {
					yPercent: this.reverseDirection ? direction * 100 : -direction * 100,
					onComplete: () => gsap.set(currentItem, { opacity: 0 }),
				})
				.to(
					currentInner,
					{
						yPercent: this.reverseDirection ? -direction * 30 : direction * 30,
						startAt: {
							rotation: 0,
						},
						rotation: -direction * 15,
						scaleY: 2.8,
					},
					0
				)
				.to(
					upcomingItem,
					{
						startAt: {
							opacity: 1,
							yPercent: this.reverseDirection ? -direction * 80 : direction * 80,
						},
						yPercent: 0,
					},
					0
				)
				.to(
					upcomingInner,
					{
						startAt: {
							yPercent: this.reverseDirection ? direction * 30 : -direction * 30,
							scaleY: 2.8,
							rotation: direction * 15,
						},
						yPercent: 0,
						scaleY: 1,
						rotation: 0,
					},
					0
				)
		}
	}

	// Background slider
	const sliderBG = new Slider(document.querySelector('.slider--bg'))

	// Foreground slider (passing true as the second argument to reverse the animation)
	const sliderFG = new Slider(document.querySelector('.slider--fg'), true)

	// text
	const feauteredItems = document.querySelectorAll('.featured-works__item')
	feauteredItems[0].classList.add('active')

	let navigate

	feauteredItems.forEach((item) => {
		const text = [...item.querySelectorAll('.f-anime-text')].filter(
			(el) => !el.closest('.featured-works__item-link')
		)

		new SplitText(text, {
			type: 'chars, words',
			charsClass: 'char',
			wordsClass: 'word',
		})

		gsap.set(text, { 'will-change': 'transform' })

		// Navigation
		navigate = (action) => {
			toggletext(action)
			sliderBG[action]()
			sliderFG[action]()
		}

		// Switch text
		const toggletext = (action) => {
			const current = sliderBG.current
			const textCurrentLetters = feauteredItems[current].querySelectorAll('.char')

			const upcoming =
				action === 'next'
					? current < sliderBG.itemsTotal - 1
						? current + 1
						: 0
					: current > 0
					? current - 1
					: sliderBG.itemsTotal - 1

			const textUpcomingLetters = feauteredItems[upcoming].querySelectorAll('.char')

			// change text
			const duration = 1.1
			// reverse logic for the previous action
			const reverse = action === 'next' ? -1 : 1

			gsap
				.timeline({
					defaults: { duration: duration, ease: 'power3.inOut' },
					onStart: () => {
						feauteredItems[upcoming].classList.add('active')
					},
					onComplete: () => {
						feauteredItems[current].classList.remove('active')
					},
				})
				.to(feauteredItems[current], {
					yPercent: reverse * 100,
					rotate: reverse * 10,
				})
				.to(
					textCurrentLetters,
					{
						duration: duration / 2,
						ease: 'power3.in',
						startAt: {
							transformOrigin: action === 'next' ? '50% 100%' : '50% 0%',
						},
						yPercent: reverse * 50,
						scaleY: 2,
					},
					0
				)
				.to(
					textCurrentLetters,
					{
						duration: duration / 2,
						ease: 'expo',
						yPercent: reverse * 100,
						scaleY: 1,
					},
					duration / 2
				)

				.to(
					feauteredItems[upcoming],
					{
						startAt: {
							yPercent: reverse * -100,
							rotate: reverse * -10,
						},
						yPercent: 0,
						rotate: 0,
					},
					0
				)
				.to(
					textUpcomingLetters,
					{
						duration: duration / 2,
						ease: 'power3.in',
						startAt: {
							transformOrigin: action === 'next' ? '50% 0%' : '50% 100%',
							yPercent: reverse * -100,
							scaleY: 1,
						},
						yPercent: reverse * -50,
						scaleY: 2,
					},
					0
				)
				.to(
					textUpcomingLetters,
					{
						duration: duration / 2,
						ease: 'expo',
						yPercent: 0,
						scaleY: 1,
					},
					duration / 2
				)
		}
	})

	window.addEventListener('load', () => {
		gsap
			.timeline({
				scrollTrigger: {
					trigger: '.featured-works__container',
					scrub: true,
					anticipatePin: 1,
					pin: true,
					start: 'center center',
					end: () => `+=${8 * window.innerHeight}`,
				},
			})
			.to(
				'.featured-works__item',
				{
					onComplete: () => {
						navigate('next')
					},
				},
				0
			)
			.to(
				'.featured-works__item',
				{
					onComplete: () => {
						navigate('next')
					},
					onReverseComplete: () => {
						navigate('prev')
					},
				},
				'>'
			)
			.to(
				'.featured-works__item',
				{
					onComplete: () => {
						navigate('next')
					},
					onReverseComplete: () => {
						navigate('prev')
					},
				},
				'>'
			)
			.to(
				'.featured-works__item',
				{
					onComplete: () => {
						navigate('next')
					},
					onReverseComplete: () => {
						navigate('prev')
					},
				},
				'>'
			)
			.to(
				'.featured-works__item',
				{
					onReverseComplete: () => {
						navigate('prev')
					},
				},
				'>'
			)

		gsap.to('.featured-works__progressbar-fill', {
			width: '100%',
			ease: 'none',
			scrollTrigger: {
				trigger: '.featured-works__container',
				scrub: true,
				start: 'center center',
				end: () => `+=${8 * window.innerHeight}`,
			},
		})
	})
}
