import { desktop, mobile } from './check-data.js'

import gsap from 'gsap'
import ScrollTrigger from 'gsap/ScrollTrigger'

gsap.registerPlugin(ScrollTrigger)

// Titles
const fixedTitleContainer = document.querySelectorAll('[data-title-container]')

if (desktop && fixedTitleContainer.length) {
	fixedTitleContainer.forEach((container) => {
		const title = container.querySelector('[data-title-fixed]')

		ScrollTrigger.create({
			trigger: title,
			start: 'bottom center',
			endTrigger: container,
			end: 'bottom center',
			pin: true,
			anticipatePin: 1,
			scrub: true,
		})
	})
}
//

// Hero founder image
const fixedImage = document.querySelector('.hero__founder-image')

if (desktop && fixedImage) {
	ScrollTrigger.create({
		trigger: fixedImage,
		start: 'top center',
		endTrigger: '.info__container',
		end: 'top center',
		pin: true,
		anticipatePin: 2,
		scrub: true,
	})
}
//

// Catalog filter
const catalogFilter = document.querySelector('.catalog__filter')

if (desktop && catalogFilter) {
	ScrollTrigger.create({
		trigger: catalogFilter,
		start: 'top top',
		endTrigger: '.catalog__container',
		end: 'bottom bottom-=40rem',
		pin: true,
		anticipatePin: 1,
		scrub: true,
	})
}
//

// Project info
const projectInfo = document.querySelector('.project__container')

if (desktop && projectInfo) {
	ScrollTrigger.create({
		trigger: projectInfo,
		start: 'top top',
		endTrigger: '.project__images',
		end: 'bottom bottom-=40rem',
		pin: true,
		anticipatePin: 1,
		scrub: true,
	})
}
//

// Project nav
const projectNav = document.querySelector('.project__nav')

if (mobile && projectNav) {
	ScrollTrigger.create({
		trigger: projectNav,
		start: 'bottom bottom-=60rem',
		endTrigger: document.body,
		end: 'bottom bottom',
		pin: true,
		anticipatePin: 1,
		scrub: true,
	})
}
//
