import gsap from 'gsap'

const lang = document.querySelector('.header__lang')
const langHead = lang.querySelector('.header__lang-heading')
const langdropdown = lang.querySelector('.header__lang-dropdown')

langHead.addEventListener('click', () => {
	let langHeadActive = langHead.classList.contains('active')

	if (!langHeadActive) {
		langHead.classList.add('active')

		gsap.to(langdropdown, {
			duration: 1,
			translateX: 0,
			ease: 'power4.inOut',
		})
	} else if (langHeadActive) {
		langHead.classList.remove('active')

		gsap.to(langdropdown, {
			duration: 1,
			translateX: '200%',
			ease: 'power4.inOut',
		})
	}
})
