import gsap from 'gsap'
import ScrollTrigger from 'gsap/ScrollTrigger'
import { desktop, homePage } from './check-data'

gsap.registerPlugin(ScrollTrigger)

const video = document.querySelector('.hero__video')
const videoWrapper = document.querySelector('.hero__video-wrapper')
const videoSection = document.querySelector('.video-section')

if (desktop && homePage) {
	const from = videoWrapper.getBoundingClientRect()
	videoSection.appendChild(videoWrapper)
	const to = videoWrapper.getBoundingClientRect()

	gsap.fromTo(
		videoWrapper,
		{
			x: from.left - to.left,
			y: from.top - to.top,
		},
		{
			x: 0,
			y: 0,
			scrollTrigger: {
				trigger: '.hero',
				start: 'bottom bottom',
				endTrigger: videoSection,
				end: 'top 20%',
				scrub: true,
			},
		}
	)

	gsap.to(video, {
		width: '100vw',
		height: '110vh',
		scrollTrigger: {
			trigger: videoSection,
			start: 'top-=150rem center',
			end: '70% 80%',
			scrub: true,
		},
	})
}
